/* eslint-disable import/max-dependencies */
import "../s.js"
import { hrefToPathname } from "@jsenv/href"
import { pathnameToOperatingSystemPath } from "@jsenv/operating-system-path"
import { fromFunctionReturningNamespace, fromHref } from "../platform/registerModuleFrom/index.js"
import { valueInstall } from "../platform/valueInstall.js"
import { createRequireFromPath } from "./createRequireFromPath.js"
import { isNativeNodeModuleBareSpecifier } from "./isNativeNodeModuleBareSpecifier.js"
import { fetchSource } from "./fetchSource.js"
import { evalSource } from "./evalSource.js"

const GLOBAL_SPECIFIER = "global"

export const createNodeSystem = ({
  compileServerOrigin,
  projectPathname,
  compileIntoRelativePath,
  resolveImport,
  executionId,
} = {}) => {
  if (typeof global.System === "undefined") throw new Error(`global.System is undefined`)

  const nodeSystem = new global.System.constructor()

  nodeSystem.resolve = (specifier, importer) => {
    return resolveImport(specifier, importer)
  }

  nodeSystem.instantiate = async (href, importerHref) => {
    if (href === GLOBAL_SPECIFIER) {
      return fromFunctionReturningNamespace(() => global, {
        href,
        importerHref,
      })
    }

    if (isNativeNodeModuleBareSpecifier(href)) {
      return fromFunctionReturningNamespace(
        () => {
          // eslint-disable-next-line import/no-dynamic-require
          const moduleExportsForNativeNodeModule = require(href)
          return moduleExportsToModuleNamespace(moduleExportsForNativeNodeModule)
        },
        { href, importerHref },
      )
    }

    return fromHref({
      href,
      importerHref,
      fetchSource,
      instantiateJavaScript: (responseBody, responseUrl) => {
        const uninstallSystemGlobal = valueInstall(global, "System", nodeSystem)
        try {
          evalSource(
            responseBody,
            responseUrlToSourceUrl(responseUrl, {
              compileServerOrigin,
              projectPathname,
              compileIntoRelativePath,
            }),
          )
        } finally {
          uninstallSystemGlobal()
        }

        return nodeSystem.getRegister()
      },
      executionId,
    })
  }

  // https://github.com/systemjs/systemjs/blob/master/docs/hooks.md#createcontexturl---object
  nodeSystem.createContext = (url) => {
    const resolve = (specifier) => {
      const urlResolved = resolveImport(specifier, url)
      return urlToOriginalUrl(urlResolved, {
        compileServerOrigin,
        projectPathname,
        compileIntoRelativePath,
      })
    }

    const originalUrl = urlToOriginalUrl(url, {
      compileServerOrigin,
      projectPathname,
      compileIntoRelativePath,
    })

    const require = createRequireFromPath(
      pathnameToOperatingSystemPath(
        originalUrl.startsWith("file://") ? hrefToPathname(originalUrl) : projectPathname,
      ),
    )
    return {
      url: originalUrl,
      resolve,
      require,
    }
  }

  return nodeSystem
}

const responseUrlToSourceUrl = (responseUrl, { compileServerOrigin, projectPathname }) => {
  if (responseUrl.startsWith(`${compileServerOrigin}/`)) {
    const ressource = responseUrl.slice(compileServerOrigin.length)
    const pathname = `${projectPathname}${ressource}`
    return pathnameToOperatingSystemPath(pathname)
  }
  if (responseUrl.startsWith("file:///")) {
    return pathnameToOperatingSystemPath(responseUrl)
  }
  return responseUrl
}

const urlToOriginalUrl = (
  url,
  { compileServerOrigin, projectPathname, compileIntoRelativePath },
) => {
  if (!url.startsWith(`${compileServerOrigin}/`)) {
    return url
  }

  if (url === compileServerOrigin) {
    return url
  }

  const ressource = url.slice(compileServerOrigin.length)
  if (!ressource.startsWith(`${compileIntoRelativePath}/`)) {
    return url
  }

  const ressourceAfterCompileInto = ressource.slice(`${compileIntoRelativePath}/`.length)
  const nextSlashIndex = ressourceAfterCompileInto.indexOf("/")
  if (nextSlashIndex === -1) {
    return url
  }

  const ressourceAfterCompileId = ressourceAfterCompileInto.slice(nextSlashIndex)

  return `file://${projectPathname}${ressourceAfterCompileId}`
}

const moduleExportsToModuleNamespace = (moduleExports) => {
  // keep in mind moduleExports can be a function (like require('stream'))
  if (typeof moduleExports === "object" && "default" in moduleExports) {
    return moduleExports
  }

  return {
    ...moduleExports,
    default: moduleExports,
  }
}
