export const spellMissingContentTypeForModule = ({
  href,
  importerHref,
}) => `Module handled as text because of missing content-type.
--- href ---
${href}
--- importer href ---
${importerHref}`

export const spellUnexpectedContentTypeForModule = ({ contentType, href, importerHref }) =>
  `Module handled as text because of unexpected content-type.
--- content-type ---
${contentType}
--- href ---
${href}
--- importer href ---
${importerHref}`
